import React, { Component } from 'react';
import ReactHtmlParser from 'react-html-parser';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { GET_NETWORK_ICON, GET_NETWORK_NAME } from '../../../../../Services/Utils/utils';

export default class TextReview extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
      // const image = await loadImage(imgUrl);
      // this.setState({
      //     height: (image.height * 100) / image.width,
      //     width: (image.width * 100) / image.height
      // })
    }
  }
  onErrorImageUpdate = event => event.target.src = "https://cloud.taggbox.com/widget/themes/media/images/no-image.svg"

  render() {

    const { socialNetwork, asset } = this.props
    const imageStyle = {
      paddingBottom: '100%', width: '100%'
    };
    var rating = [];
    if (asset.rating > 0) {
      rating = new Array(asset.rating).fill(0);
    }
    return <div className="position-relative rounded-1 bg-light overflow-hidden w-100" style={imageStyle}>
      <div className="position-absolute  w-100 p-2 translate-middle top-50 start-50">
        <div className="assets_social block-center py-2">

          {
            socialNetwork && socialNetwork.length > 0 ? <img className="img-fluid" src={baseUrl(`media/icons/social-icons/${GET_NETWORK_ICON(socialNetwork[0].id)}.svg`)} height={20} width={20} alt={GET_NETWORK_NAME(socialNetwork[0].id)} /> : null
          }
        </div>
        <div className="review block-center ps-1 mb-2">
          {
            rating.map((item, index) => <i key={index} className="ri-star-fill icon-4 me-1" style={{color:'#FBBB01'}}></i>)
          }
        </div>
        <div className="overflow-hidden h-100">
          <p className="text-center line-clamp-4 line-clamp fs-7">{ReactHtmlParser(asset.content)}</p>
        </div>
      </div>
    </div>
  }
}
