import React, { Component } from 'react';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import Image from './Image';
import Video from './Video';
import TextReview from './TextReview';

export default class Media extends Component {
  constructor(props) {
    super(props);
    this.state = {
      imgHeight: '100',
      imgWidth: '100',
      error: '',
      imgUrl: props.url
    }
  }

  componentWillMount() {
    this.onMediaLoad(this.props)
  }

  onMediaLoad = async (props) => {
    const { height, width } = props;

    if (height && height !== "" && width && width !== "") {
      this.setState({
        imgHeight: (parseInt(height) * 100) / parseInt(width),
        imgWidth: (parseInt(width) * 100) / parseInt(height)
      })
    } else {
    }
  }
  onErrorImageUpdate = event => event.target.src = baseUrl(`media/images/error/image.svg`)

  render() {
    const { asset, socialNetwork } = this.props
    return <div className="position-relative rounded-bottom-sm bg-light overflow-hidden w-100" style={{opacity:asset.status==2 ? '.5' :'1'}}>
            {
            asset.type == 1 ? <TextReview asset={asset} socialNetwork={socialNetwork} /> :
              (asset.type == 2 || asset.type == 4) ? <Image height={asset.mediaHeight} width={asset.mediaWidth} url={asset.postFile} postFile={asset.postFile} alt={asset.name} asset={asset} socialNetwork={socialNetwork} recommendation={asset.recommendation} />
                : (asset.type == 3 || asset.type == 5) ? <Video asset={asset} socialNetwork={socialNetwork} recommendation={asset.recommendation} /> : null }
                
              {asset.status==2?
                <div className='w-100 py-2 position-absolute bottom-0 start-0 block-center text-light-alt fs-10 fw-medium bg-danger'>
                    <i className="ri-eye-off-line me-1 fs-8"></i> This Asset is Hidden
                </div>
              : null}
          </div>
  }
}
